/*** START: For breadcrumb sidebar ***/

.sac-breadcrumb {
    position: relative;
    background-color: var(--black);
    nav {
        a {
            color: var(--white);
            &:hover {
                color: var(--graylt);
            }
            &:first-child,
            &:only-child {
                color: var(--hgob);
            }
            &:nth-child(2) {
                color: var(--hgobmd);
            }
            &:nth-child(3) {
                color: var(--hgoblt);
            }
            &:last-child,
            &:only-child {
                color: var(--white) !important;
            }
            &:after {
                color: var(--graymd);
            }
        }
        &.navbar {
            display: flex;
            width: 100%;
            // margin: 0 auto;
            // align-items: space-between;
            max-width: calc(1400px - 8%);
            padding: .5rem 5%;
            position: relative;
            justify-content: space-between;
            @media screen and (min-width: 1400px) {
                padding: 0;
            }
            div {
                display: flex;
                // width: 100%;
                // margin: 0 auto;
                // align-items: flex-start;
                // width: 80%;
                // max-width: calc(1400px - 8%);
                a {
                    font-weight: normal;
                    font-weight: 500;
                    color: var(--hgoblt);
                    display: block;
                    font-size: .875rem;
                    text-decoration: none;
                    padding: calc(.5rem + 6px) 0;
                    @media screen and (min-width: 700px) {
                        padding: calc(1rem + 6px) 0;
                    }
                    &:hover {
                        color: var(--graylt);
                    }
                    &::after {
                        content: 'âž¤';
                        display: inline;
                        color: #aaa;
                        margin: 0 1ch;
                    }
                    &:last-child {
                        // padding-bottom: 1rem;
                    }
                    &:last-child,
                    &:only-child {
                        color: #fff;
                        &::after {
                            display: none;
                        }
                    }
                    &:first-child {
                        color: var(--hgo); //padding-top: 1rem;
                    }
                    &:nth-child(2) {
                        color: var(--hgobmd);
                    }
                }
            }
            [href="#connect"] {
                display: block;
                font-size: .875rem;
                font-weight: 600;
                padding: .5rem 2ch;
                background: var(--stgr);
                color: var(--white);
                border: 1px solid var(--hgob);
                border-radius: 4px;
                text-decoration: none;
            }
        }
    }
}